            "sinfo":"Jules Verne, (born February 8, 1828, Nantes, France—died March 24, 1905, Amiens), prolific French author whose writings laid much of the foundation of modern science fiction Verne’s father, intending that Jules follow in his footsteps as an attorney, sent him to Paris to study law. But the young Verne fell in love with literature, especially theatre. He wrote several plays, worked as secretary of the Théâtre Lyrique (1852–54), and published short stories and scientific essays in the periodical Musée des familles. In 1857 Verne married and for several years worked as a broker at the Paris Stock Market. During this period he continued to write, to do research at the Bibliothèque Nationale (National Library), and to dream of a new kind of novel—one that would combine scientific fact with adventure fiction. In September 1862 Verne met Pierre-Jules Hetzel, who agreed to publish the first of Verne’s Voyages extraordinaires (“Extraordinary Journeys”)—Cinq semaines en ballon (1863; Five Weeks in a Balloon). Initially serialized in Hetzel’s Le Magasin d’éducation et de récréation, the novel became an international best seller, and Hetzel offered Verne a long-term contract to produce many more works of “scientific fiction.” Verne subsequently quit his job at the stock market to become a full-time writer and began what would prove to be a highly successful author-publisher collaboration that lasted for more than 40 years and resulted in more than 60 works in the popular series Voyages extraordinaires. "
